import React from 'react';
import { Helmet } from 'react-helmet';
import { useIntl } from 'gatsby-plugin-intl';
import { useLocation } from '@reach/router';
import { WebSite } from 'schema-dts';
import { JsonLd } from 'react-schemaorg';
import urljoin from 'url-join';
import { useStaticQuery, graphql } from 'gatsby';
import { Query } from '../../graphql-types';

export const SEO: React.FunctionComponent<{
  title: string;
  description: string;
  image?: string;
  isArticle?: boolean;
  keywords?: string;
}> = ({ title, description, image, isArticle = false,keywords }) => {
  const location = useLocation();
  const intl = useIntl();
  const {
    site: {
      siteMetadata: { siteUrl },
    },
  } = useStaticQuery<Query>(
    graphql`
      query {
        site {
          siteMetadata {
            siteUrl
          }
        }
      }
    `,
  );

  title = title ? `${title} | Aviaco GSE` : `Aviaco GSE`;
  if (description?.length > 150) {
    description = description.slice(0, 150) + ' ...';
  }

  const twitter = '@aviacogse';
  const locale = intl.locale;
  const canonicalUrl = urljoin(siteUrl, location.pathname);
  const imageUrl = urljoin(siteUrl, image ? image : '/img/logo.png');

  const ogType = isArticle ? 'article' : 'website';

  return (
    <Helmet title={title}>
      <html lang={locale} />
      <meta name="description" content={description} />
      <meta name="keywords" content={keywords} />
      <meta name="image" content={imageUrl} />
      <meta property="og:locale" content={locale} />
      <meta property="og:url" content={canonicalUrl} />
      <meta property="og:type" content={ogType} />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={description} />
      <meta property="og:image" content={imageUrl} />
      <meta property="og:image:alt" content={description} />
      <meta name="twitter:creator" content={twitter} />
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta name="twitter:image" content={imageUrl} />
      <meta name="twitter:image:alt" content={description} />
      <link rel="canonical" href={canonicalUrl} />
      <JsonLd<WebSite>
        item={{
          '@context': 'https://schema.org',
          '@type': 'WebSite',
          url: siteUrl,
          name: title,
        }}
      />
      ;
    </Helmet>
  );
};
